import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {UserService} from '../user/user.service';
import {MenuItem, MenuItemType} from '../common/menu/menu.component';
import {UserCenterService} from './ucenter/user-center.service';
import {of, Subject} from 'rxjs';
import {debounceTime, distinctUntilChanged, switchMap} from 'rxjs/operators';
import {Result} from '../common/result';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  showMenu = false;
  showUserMenu = false;

  // 
  menuData: MenuItem[] = [
    {label: '', url: 'http://uxd.kaikeba.com', type: MenuItemType.Link},
    {label: '', url: 'http://pm.kaikeba.com', type: MenuItemType.Link},
  ];
  userMenuData: MenuItem[] = [
    {label: '', url: 'usercenter/course', type: MenuItemType.Route},
    {label: '', url: 'usercenter/message', type: MenuItemType.Route},
    {
      label: '', cb: () => {
        // 
        // ...
        this.us.logout().subscribe(
          (result: boolean) => {
            if (result) {
              // 
              this.router.navigate(['/user/login']);
            }
          }
        );
      }, type: MenuItemType.Callback
    },
  ];

  // 
  searchResult: MenuItem[];
  subject: Subject<string> = new Subject<string>();

  constructor(private router: Router,
              private us: UserService,
              private ucs: UserCenterService) {
    // subject
    this.subject.pipe(
      debounceTime(300), // 
      distinctUntilChanged(), // 
      switchMap(keyword => { // Observable<string>Observable<Result<any>>
        // 
        if (keyword === '') {
          return of(null);
        }
        return this.ucs.searchCourse(keyword);
      })
    ).subscribe(// 
      (result: Result<any[]>) => {
        if (result && result.success) {
          // MenuItem
          // 
          // 
          // mapMenuItem[], 
          this.searchResult = result.data.map(item => {
            return {
              label: item.name,
              url: item.url,
              type: MenuItemType.Link
            };
          });
        } else {
          this.searchResult = null;
        }
      }
    );
  }

  ngOnInit() {
  }

  gotoCourse(courseId) {
    // path
    // 
    // 
    this.router.navigate(['/main/course/', courseId, {
      username: 'tom', age: 20,
    }], {
      queryParams: {foo: 'bar'}
    });
  }

  search(keyword) {
    this.subject.next(keyword);
  }
}
