import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Result} from '../../../common/result';
import {map} from 'rxjs/operators';
import {of} from 'rxjs';

export interface Course {
  id: number;
  name: string;
  phase: string;
  poster: string;
}

// 
export interface Pandect {
  study_time: number;
  rank: number;
  training: string;
  graduation_time: Date;
}

// 
export interface Stage {
  id: number;
  name: string;
  title: string;
  sub_title: string;
  state: number; // 0-1-2-
  videos: string; // id
}


@Injectable({
  providedIn: 'root'
})
export class CourseService {
  url = '/api/users/';

  courses: Course[] = null;
  pandect: Pandect = null;

  constructor(private http: HttpClient) {
  }

  // 
  getMyCourses() {
    if (this.courses) { // 
      return of(this.courses);
    }
    return this.http.get<Result<Course[]>>(this.url + 'my-courses')
      .pipe(
        map((result: Result<Course[]>) => {
          // 
          this.courses = result.data;
          return result.data;
        })
      );
  }

  // classId
  getCourseById(id) {
    if (this.courses) {
      // 
      const course = this.courses.find(c => c.id === +id);
      return of(course);
    }
    // 
    return this.http.get<Result<Course>>(this.url + 'my-course/' + id)
      .pipe(
        map((result: Result<Course>) => {
          return result.data;
        })
      );
  }


  //  
  getPandect(classId) {
    if (this.pandect) {
      return of(this.pandect);
    }

    return this.http.get(this.url + 'pandect/' + classId)
      .pipe(
        map((result: Result<Pandect>) => {
          console.log(result.data);
          if (result.success) {
            this.pandect = result.data;
            return this.pandect;
          } else {
            this.pandect = null;
            return null;
          }
        })
      );
  }

  // 
  getStages(classId) {
    return this.http.get(this.url + 'stages/' + classId)
      .pipe(
        map((r: Result<Stage[]>) => {
          if (r.success) {
            return r.data;
          } else {
            return null;
          }
        })
      );
  }

}
